<template>
  <div>
    <form action="/">
      <van-search
        v-model="value"
        show-action
        placeholder="女神节跨店139减70"
        @input="onSearch"
        @cancel="onCancel"
      />
    </form>
    <!-- 搜索功能 -->
    <van-cell-group v-for="(item,index) in searchList" :key="index">
      <van-cell
        v-for="(list,i) in item"
        :key="i"
        :value="list[0]"
        @click="search(i,list[0])"
      />
    </van-cell-group>
    <!-- 历史搜索 -->
    <div class="box" v-show="bool">
      <div class="title">历史搜索</div>
      <ul class="list" >
        <li v-for="(item,index) in history" :key="index"  @click="$router.push({path:'/shoppingClass',query:{text:item}})">{{item}}</li>
      </ul>
    </div>

    <!-- 热搜 -->
    <div class="box" v-show="bool">
      <div class="title">热搜</div>
      <ul class="list">
        <li v-for="(item,index) in hotSeach" :key="index" @click="$router.push({path:'/shoppingClass',query:{text:item}})">{{item}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value: "",
      bool: true,
      searchList: [],
      history:[],
      hotSeach:[]
    };
  },
  methods: {
    onSearch() {
      this.$jsonp("https://suggest.taobao.com/sug", {
        code: "utf-8",
        q: this.value
      }).then(res => {
        // console.log(res);
        this.searchList = res;
      });
    },
    onCancel() {
      this.$store.state.bool = true;
      this.$router.go(-1);
    },
    search(i,list){
      // console.log(i);
      this.$router.push({path:'/shoppingClass',query:{text:list}})
      this.$store.state.myjp.history.push(list)
    }
  },
  watch: {
    value(newVal) {
      // console.log(newVal);
      newVal.length > 0 ? (this.bool = false) : (this.bool = true);
    }
  },
  created() {
    this.$store.state.bool = false;
    this.history = this.$store.state.myjp.history
    this.hotSeach = this.$store.state.myjp.hotSeach
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    // this.$store.state.bool = true;
  }
};
</script>

<style lang="scss" scoped>
.van-search {
  padding: 5px;
}
.title {
  color: #000;
  font-size: 16px;
  padding: 0 13px;
  margin-top: 10px;
  font-weight: bold
}
.list {
  padding: 0 13px;
  &:after {
    display: block;
    content: "";
    clear: both;
  }
  li {
    color: #999;
    padding: 5px 10px;
    margin-top: 8px;
    margin-bottom: 3px;
    margin-right: 10px;
    font-size: 12px;
    background-color: #f4f4f4;
    float: left;
    border-radius: 5px;
  }
}
.van-cell {
  padding: 0 0 0 16px;
}
</style>